.EducationComponent {
  margin-top: 16vw; }
  .EducationComponent-pagination {
    text-align: center;
    margin-bottom: 2vw; }
    .EducationComponent-pagination-container {
      display: inline-block;
      border-top: 2px #ccc solid;
      border-bottom: 2px #ccc solid;
      border-left: 2px #ccc solid; }
    .EducationComponent-pagination-detail {
      display: inline-block;
      width: 2vw;
      font-size: 2vw;
      border-right: 2px #ccc solid;
      cursor: pointer; }
  .EducationComponent-month {
    text-align: center; }
    .EducationComponent-month-container {
      display: inline-block;
      margin: 0 auto 2vw auto; }
    .EducationComponent-month-prev-next {
      font-size: 4vw;
      font-weight: 700;
      color: black;
      border: 2px solid #ccc;
      display: inline-block;
      margin-left: 1vw;
      margin-right: 1vw;
      padding-left: 0.5vw;
      padding-right: 0.5vw;
      cursor: pointer; }
    .EducationComponent-month-current {
      font-size: 4vw;
      font-weight: 700;
      color: #4d328e;
      display: inline-block; }



