.FestivalComponent {
  margin-top: 16vw;
  text-align: left; }
  .FestivalComponent-pagination {
    text-align: center;
    margin-bottom: 2vw; }
    .FestivalComponent-pagination-container {
      display: inline-block;
      border-top: 2px #ccc solid;
      border-bottom: 2px #ccc solid;
      border-left: 2px #ccc solid; }
    .FestivalComponent-pagination-detail {
      display: inline-block;
      width: 2vw;
      font-size: 2vw;
      border-right: 2px #ccc solid;
      cursor: pointer; }
  .FestivalComponent-month {
    text-align: center; }
    .FestivalComponent-month-container {
      display: inline-block;
      margin: 0 auto 2vw auto; }
    .FestivalComponent-month-prev-next {
      font-size: 4vw;
      font-weight: 700;
      color: black;
      border: 2px solid #ccc;
      display: inline-block;
      margin-left: 1vw;
      margin-right: 1vw;
      padding-left: 0.5vw;
      padding-right: 0.5vw;
      cursor: pointer; }
    .FestivalComponent-month-current {
      font-size: 4vw;
      font-weight: 700;
      color: #4d328e;
      display: inline-block; }
  .FestivalComponent-content {
    background-color: white;
    padding-bottom: 2vw; }
  .FestivalComponent-title-text {
    width: 40%;
    margin-left: 2.5%;
