    padding-top: 2vw;
    padding-bottom: 2vw; }
  .FestivalComponent-search-container {
    width: 70%;
    margin: 0 auto;
    border: #ccc solid 3px;
    text-align: left; }
    .FestivalComponent-search-container input {
      width: 98%;
      border: none;
      font-size: 3vw;
      padding: 0.5vw; }
      .FestivalComponent-search-container input:focus {
        outline: none; }
  .FestivalComponent-content-container {
