      padding-top: 3vw;
      padding-bottom: 2vw; }
      .FestivalComponent-card-description-title h1 {
        margin: 0px;
        font-size: 3vw; }
    .FestivalComponent-card-description-content h5 {
      padding-bottom: 1vw;
      margin: 0px;
      font-size: 2vw; }
    .FestivalComponent-card-description-content-type1 {
      color: green;
      border: 2px solid #ccc; }
    .FestivalComponent-card-description-content-type2 {
      margin-left: 1vw;
      color: blue;
      border: 2px solid #ccc; }
    .FestivalComponent-card-description-button-detail {
      background-color: #009bff;
      color: white;
      border: none;
      padding: 1vw 2vw;
      font-size: 2.5vw;
      border-radius: 1vw;
      cursor: pointer; }
      .FestivalComponent-card-description-button-detail:focus {
        outline: none; }
    .FestivalComponent-card-description-button-apply {
      background-color: #4d328e;
      margin-left: 1vw;
      color: white;
      border: none;
      padding: 1vw 2vw;
      font-size: 2.5vw;
      border-radius: 1vw;
      cursor: pointer; }
      .FestivalComponent-card-description-button-apply:focus {
        outline: none; }



