.InformationComponent {
  margin-top: 16vw; }
  .InformationComponent-title-text {
    width: 50%;
    display: inline-block;
    color: #551c93;
    font-size: 4.5vw;
    padding-top: 2vw;
    text-align: left;
    padding-bottom: 2vw; }
