.ContainerComponent {
  margin-bottom: 2vw; }
  .ContainerComponent table {
    width: 100%;
    border-top: 1px solid #551c93;
    border-collapse: collapse; }
    .ContainerComponent table th {
      background-color: #551c93;
      color: #ccc;
      font-size: 1.5vw; }
    .ContainerComponent table td {
      border-bottom: 1px solid #ccc;
      font-size: 1.5vw; }
      .ContainerComponent table td a {
        color: black;
        text-decoration: none; }
        .ContainerComponent table td a:hover {
          color: blue; }
      .ContainerComponent table td img {
        width: 50vw; }
  .ContainerComponent-pagination {
    text-align: left;
    padding-left: 2vw;
    padding-right: 2vw; }
    .ContainerComponent-pagination a {
      text-decoration: none;
      color: black; }



