.LoginComponent {
  margin-top: 16vw; }
  .LoginComponent-content {
    border-top: 1px solid #ccc;
    padding-top: 2vw;
    padding-bottom: 2vw; }
  .LoginComponent-content-detail {
    border: 1px solid #ccc;
    display: inline-block;
    padding: 3vw;
    text-align: left; }
    .LoginComponent-content-detail-id {
      margin-bottom: 2vw;
      font-size: 2vw; }
      .LoginComponent-content-detail-id input {
        width: 25vw;
        font-size: 2vw; }
    .LoginComponent-content-detail-password {
      margin-bottom: 2vw;
      font-size: 2vw; }
      .LoginComponent-content-detail-password input {
        width: 25vw;
        font-size: 2vw; }
    .LoginComponent-content-detail-button {
      text-align: center; }
      .LoginComponent-content-detail-button button {
        width: 10vw;
        font-size: 1.5vw;
        border: none;
        padding: 1vw;
        background-color: #4d328e;
        color: white;
        border-radius: 1vw;
        cursor: pointer; }



