.ActivityComponent {
  margin-top: 16vw; }
  .ActivityComponent-pagination {
    text-align: center;
    margin-bottom: 2vw; }
    .ActivityComponent-pagination-container {
      display: inline-block;
      border-top: 2px #ccc solid;
      border-bottom: 2px #ccc solid;
      border-left: 2px #ccc solid; }
    .ActivityComponent-pagination-detail {
      display: inline-block;
      width: 2vw;
      font-size: 2vw;
      border-right: 2px #ccc solid;
      cursor: pointer; }
  .ActivityComponent-title-text {
    width: 50%;
    display: inline-block;
    color: #551c93;
    font-size: 4.5vw;
    padding-top: 2vw;
    text-align: left;
    padding-bottom: 2vw; }
  .ActivityComponent-title-category {
    width: 40%;
    margin-right: 5vw;
    text-align: right;
    display: inline-block;
    font-size: 1.75vw; }
    .ActivityComponent-title-category a {
      text-decoration: none;
      color: blue; }
  .ActivityComponent-search {
    padding-top: 2vw;
    padding-bottom: 2vw; }
  .ActivityComponent-search-container {
    width: 70%;
    margin: 0 auto;
    border: #ccc solid 3px;
    text-align: left; }
    .ActivityComponent-search-container input {
      width: 98%;
      border: none;
      font-size: 3vw;
      padding: 0.5vw; }
      .ActivityComponent-search-container input:focus {
        outline: none; }
  .ActivityComponent-content {
    text-align: center;
    margin-bottom: 2vw; }
    .ActivityComponent-content a {
      color: black; }
  .ActivityComponent-content-card {
    width: 30vw;
    margin: 1vw 0.5vw;
    display: inline-block;
    border: 1px solid #ccc; }
  .ActivityComponent-image-title {
    padding-top: 2vw;
    padding-bottom: 2vw; }
    .ActivityComponent-image-title h6 {
      margin: 0px;
      font-size: 2vw; }
  .ActivityComponent-image-date {
    padding-top: 1vw;
    padding-bottom: 1vw;
    background-color: #ccc; }
    .ActivityComponent-image-date h6 {
      margin: 0px;
      font-size: 2vw; }
  .ActivityComponent-image img {
    width: 100%; }



